import React from 'react';
import logo from './logo.svg';
import './App.css';
class Logo extends React.Component {
 
    render() {
   
      return (
        <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1>RAFAEL FLORES</h1>
          <h3
            className="App-link"
            target="_blank"
            rel="noopener noreferrer"
          >
            Hello World
          </h3>
          <h6>
             Soy un desarrollador web aprendiz de las nuevas tecnologias, ¿Qué esperas? Dame un besoxd
          </h6>
          </header>
    </div>
      )
    }
}
export default Logo;